import React, { useEffect, useState } from "react";
import { Card, Col, Form, Pagination, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { useNavigate } from "react-router-dom";
import DiseaseForm from "./DiseaseForm";
import PrescriptionCards from "./PrescriptionCards";
import { getAllAdminPresForm } from "../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";
import { useDispatch, useSelector } from "react-redux";
import { remainingAptPres } from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import { PATH } from "../../../utils/path";
import FullScreenLoader from "../../../authorization/FullScreenLoader";

export default function OnlinePrescForms() {
  const [currentPage, setCurrentPage] = useState(1);
  const [searchValue, setSearchValue] = useState();

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const searchParams = new URLSearchParams(window.location.search);
  const diseaseId = searchParams.get("diseaseId");
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { getAllAdminOnlinePrescripionForm, isSuccess, isLoading, isError } =
    useSelector((state) => state?.onlinePrescriptionForm);
console.log("getAllAdminOnlinePrescripionForm", getAllAdminOnlinePrescripionForm?.totalCounts)
  if (diseaseId) {
    console.log("Disease Id:", diseaseId);
  } else {
    console.log("Disease parameter not found in the URL");
  }

  const cardsPerPage = 12;

  const indexOfLastCard = currentPage * cardsPerPage;
  const indexOfFirstCard = indexOfLastCard - cardsPerPage;
  const currentCards =
    getAllAdminOnlinePrescripionForm?.data &&
    getAllAdminOnlinePrescripionForm?.data.length
      ? getAllAdminOnlinePrescripionForm?.data.slice(
          indexOfFirstCard,
          indexOfLastCard
        )
      : [];

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const customTotal = (from, to, size) => (
    <span>
      {from} to {to} out of {size} entries
    </span>
  );

  const handleClick = (cardId, cardTitle, patientFormAttempt) => {
    navigate(
      `${PATH.PATIENT_PRESCRIPTION_FORMS}?diseaseName=${cardTitle.replace(
        / /g,
        "-"
      )}&diseaseId=${cardId}`,
      {
        state: patientFormAttempt,
      }
    );
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(remainingAptPres(finalData));
  };

  useEffect(() => {
    const finalData = {
      Search: searchValue ? searchValue : "",
      PageNo: currentPage,
      Size: 12,
    };
    dispatch(getAllAdminPresForm(finalData));
  }, [searchValue, currentPage]);

  return (
    <>
      {!diseaseId ? (
        <>
          <h5>Prescription Forms</h5>
          <Card className="shadow-sm mt-4">
            {isSuccess ? (
              <>
                <Row>
                  <Col xl={3} md={6} sm={6} xs={12} className="mt-3 mx-3">
                    <div className="d-flex">
                      <Form.Control
                        type="text"
                        placeholder="Search"
                        className="me-2 search-field-spacing"
                        aria-label="Search"
                        onKeyDown={(e) => {
                          if (e.key === "Enter") {
                            setSearchValue(e.target.value);
                          }
                        }}
                        onChange={(e) => {
                          if (e.key === "Enter") {
                            setSearchValue(e.target.value);
                          }
                        }}
                      />
                      <BiSearch size={22} className="searchbar-icon" />
                    </div>
                  </Col>
                </Row>
                <PrescriptionCards
                    cards={getAllAdminOnlinePrescripionForm?.data}
                    onClick={(id, title, patientFormAttempt) =>
                      handleClick(id, title, patientFormAttempt)
                    }
                  />
                {/* {currentCards ? (
                  <PrescriptionCards
                    cards={currentCards}
                    onClick={(id, title, patientFormAttempt) =>
                      handleClick(id, title, patientFormAttempt)
                    }
                  />
                ) : (
                  <p className="text-center mt-3">No Record Found</p>
                )} */}
                  {getAllAdminOnlinePrescripionForm?.totalCounts > 0 ? (
                    <Pagination className="d-flex justify-content-between align-items-center px-3 patient__prescription-pagination">
                      <Pagination>
                        {customTotal(
                          indexOfFirstCard + 1,
                          indexOfLastCard,
                          getAllAdminOnlinePrescripionForm?.totalCounts,
                        )}
                      </Pagination>
                      <div className="d-flex justify-content-between pagination__page-Number">
                        <Pagination.Prev
                          onClick={() => paginate(currentPage - 1)}
                          disabled={currentPage === 1}
                        />
                        {Array.from({
                          length: Math.ceil(
                            getAllAdminOnlinePrescripionForm?.totalCounts /
                              cardsPerPage
                          ),
                        }).map((_, index) => (
                          <Pagination.Item
                            key={index + 1}
                            active={currentPage === index + 1}
                            onClick={() => paginate(index + 1)}
                          >
                            {index + 1}
                          </Pagination.Item>
                        ))}
                        <Pagination.Next
                          onClick={() => paginate(currentPage + 1)}
                          disabled={
                            currentPage ===
                            Math.ceil(
                              getAllAdminOnlinePrescripionForm?.totalCounts /
                                cardsPerPage
                            )
                          }
                        />
                      </div>
                    </Pagination>
                  ): (
                    <p className="text-center mt-3">No Record Found</p>
                  )}
                  
              </>
            ) : isLoading ? (
              <FullScreenLoader />
            ) : isError ? (
              <p className="text-center mt-3">Network Error...</p>
            ) : null}
          </Card>
        </>
      ) : (
        <DiseaseForm diseaseId={diseaseId} />
      )}
    </>
  );
}
